$mainColor: #ff0000;

@mixin fs() {
    background-color: #eee;
    padding: 20px;
    border-radius: 5px;
    max-width: 100%;
}

.my-alert {
    @include fs();

    &.success {
        @extend .my-alert;
        background-color: #ff0;
    }

    p {
        margin: 0;
        color: $mainColor;
    }
}

